/* eslint jsx-a11y/label-has-for:0 */

import React from "react";
import Helmet from "react-helmet";
import { Link } from "gatsby";
import styled from "styled-components";
import { Layout, Wrapper, Header } from "../components";

import config from "../../config";

const Content = styled.div`
  grid-column: 2;
  box-shadow: 0 4px 120px rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  padding: 2rem 4rem;
  background-color: ${props => props.theme.colors.bg};
  z-index: 9000;
  margin-top: -3rem;
  @media (max-width: ${props => props.theme.breakpoints.tablet}) {
    padding: 3rem 3rem;
  }
  @media (max-width: ${props => props.theme.breakpoints.phone}) {
    padding: 2rem 1.5rem;
  }
  form {
    p {
      label,
      input {
        display: block;
      }
      input {
        min-width: 275px;
        margin-top: 0.5rem;
      }
      textarea {
        resize: vertical;
        min-height: 150px;
        width: 100%;
        margin-top: 0.5rem;
      }
    }
  }
`;

const Contact = () => (
  <Layout>
    <Wrapper>
      <Helmet title={`Contact | ${config.siteTitle}`} />
      <Header>
        <Link to="/">{config.siteTitle}</Link>
      </Header>
      <Content>
        <h1>Heya!</h1>
        <div>
          I am a developer based in Dublin, Ireland. My work is spread across{" "}
          <span style={{ boxShadow: "inset 0 -3px 0 rgba(208, 46, 119, .5)" }}>
            Machine Learning
          </span>
          ,
          <span style={{ boxShadow: "inset 0 -3px 0 rgba(36,177,209,.4)" }}>
            {" "}
            Auctions
          </span>
          ,
          <span style={{ boxShadow: "inset 0 -3px 0 rgba(8,20,220,.47)" }}>
            {" "}
            Distributed Systems{" "}
          </span>
          and{" "}
          <span style={{ boxShadow: "inset 0 -3px 0 rgba(184,107,255,.5)" }}>
            {" "}
            Cryptography
          </span>
          . Currently, I work as Machine Learning researcher at Citi innovation
          Lab in Dublin. I am a recent convert to ML (last 2years), before that
          in Citi I built financial industry's first enterprise grade blockchain
          application connecting{" "}
          <a href="https://www.wsj.com/articles/citigroup-to-tap-nasdaq-for-blockchain-payment-technology-1495455969">
            Nasdaq's blockchain platform to Citi's transaction processing system
          </a>
          . I also did research and built prototypes in the field of trade
          finance, supply chain finance, digital identity and zero knowledge
          proofs. In past, I have completed Msc in Mobile and Ubiquitous
          Computing from Trinity College Dublin, did some reasearch on{" "}
          <span style={{ boxShadow: "inset 0 -3px 0 rgba(208, 46, 119, .5)" }}>
            satellite image segmentation
          </span>
          ,{" "}
          <span style={{ boxShadow: "inset 0 -3px 0 rgba(208, 46, 119, .5)" }}>
            {" "}
            design of two channel filter banks
          </span>
          ,{" "}
          <span style={{ boxShadow: "inset 0 -3px 0 rgba(109, 183, 58, .6)" }}>
            swarm based optimization techniques
          </span>
          , published{" "}
          <a href="https://www.sciencedirect.com/science/article/pii/S0957417413008919">
            here
          </a>
          ,{" "}
          <a href="https://www.sciencedirect.com/science/article/abs/pii/S0019057814001256?via%3Dihub">
            here
          </a>{" "}
          and{" "}
          <a href="https://www.sciencedirect.com/science/article/pii/S2215098615303785">
            here
          </a>
          .
        </div>
      </Content>
    </Wrapper>
  </Layout>
);

export default Contact;
